package com.londogard.timetracker

import com.londogard.timetracker.components.TodoItem
import com.londogard.timetracker.files.showOpenFilePicker
import dev.fritz2.binding.storeOf
import dev.fritz2.components.box
import dev.fritz2.components.clickButton
import dev.fritz2.components.stackUp
import dev.fritz2.dom.html.RenderContext
import dev.fritz2.dom.html.render
import dev.fritz2.dom.mount
import dev.fritz2.routing.router
import kotlinx.browser.window
import kotlinx.coroutines.ExperimentalCoroutinesApi

@ExperimentalCoroutinesApi
fun main() {
    val router = router("todo")

    render {

        script {
            type("module")
            src("https://unpkg.com/img-comparison-slider@3/dist/component/component.esm.js")
        }
        script {
            attr("nomodule", "")
            src("https://unpkg.com/img-comparison-slider@3/dist/component/component.js")
        }
        custom("link") {
            attr("rel", "stylesheet")
            attr("href", "https://unpkg.com/img-comparison-slider@3/dist/collection/styles/initial.css")
        }
        div("header") {
            p { +"Header" }
            hr { }
        }

        div("content") {
            router.data.render { site ->
                when (site) {
                    "todo" -> mainContent()
                    "time" -> timeContent()
                    else -> mainContent()
                }
            }
        }

        div("footer") {
            hr { }
            p { +"Footer" }
        }

    }.mount("target")
}

fun RenderContext.timeContent() {
    h1 { +"My App" }
    stackUp {
        items {
            box {
                TodoItem(storeOf(false)).render(this)
            } // simplified for readability
            box { p { +"2" } }
            box { p { +"3" } }

            //window.customElements.define("m3-stars", Stars::class.js.unsafeCast<() -> dynamic>())
        }
    }
}

fun RenderContext.mainContent() {
    h1 { +"My App" }
    stackUp {
        items {
            button { +"a" }.clickButton {
                showOpenFilePicker()
            }


            box {
                TodoItem(storeOf(false)).render(this)
            } // simplified for readability
            box { p { +"2" } }
            box { p { +"3" } }

            custom("img-comparison-slider") {
                img {
                    attr("slot", "before")
                    attr("width", "100%")
                    src("https://img-s-msn-com.akamaized.net/tenant/amp/entityid/BB1cEFvJ.img?h=630&w=1119&m=6&q=60&o=f&l=f")
                }
                img {
                    attr("slot", "after")
                    attr("width", "100%")
                    src("https://upload.wikimedia.org/wikipedia/commons/thumb/5/56/Donald_Trump_official_portrait.jpg/375px-Donald_Trump_official_portrait.jpg")
                }
            }
        }
    }
}